	display: flex;
	flex-direction: column;
	min-height: 100%;
	width: 100%;
	margin: 0 auto;
	overflow-y:scroll;
}
.fixed-crossbrowser-background {
    position: fixed;
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center 0;
    background-size:cover;
    background-image: url(/img/lala/London-wallpaper-9_2.jpg);
    opacity: 1;
    z-index: -1;
    top: 0;
    left: 0;
}
